<template>
  <div class="hello">
    <div class="login-wrap">
      <div class="nav_container">
        <div class="nav-centre">
          <img src="http://res.yujingceping.com/res/resources/front/imgs/Logo.png">
        </div>
      </div>
      <!--<div class="ms-title">教务管理系统</div>-->
      <div class="contentBody">
        <!--重置密码-->
        <div class="resetPass" v-if="needResetPassword">
          <div class="ms-login">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="75px" class="demo-ruleForm">
              <el-form-item prop="password" label="设置密码:">
                <el-input :maxlength="18"  v-model="ruleForm.password" placeholder="密码"></el-input>
              </el-form-item>
              <div class="login-btn">
                <el-button class="primary" type="primary" :disabled="state"  @click="submitForm('ruleForm')">保存</el-button>
                <el-button  type="text"  @click="skip()">跳过</el-button>
              </div>
            </el-form>
          </div>
        </div>
        <!--多个学校选择-->
        <div class="schoolSelect" v-else-if="schoolListIsShow">
          <h3>请选择一个学校登录操作</h3>
          <ul>
            <li class="schoolNameStyle" @click="selectSchool(school)" v-for="school in schoolList">{{school.schoolName}}</li>
          </ul>
        </div>
        <!--选择教学信息-->
        <div class="teachingMsg" v-else-if="needChooseObject">
          <div>
            <el-radio-group v-model="currentGradeId" @change="getCurrentGradeId(currentGradeId)">
              <el-radio-button v-for="(item,index) in gradeList" :label="item" :key="index"> {{item.gradeName}} </el-radio-button>
            </el-radio-group>
          </div>
          <div>
            <table v-for="(item,index) in gradeList" border="1" v-show="item.gradeId == currentGradeId.gradeId">
              <tr>
                <th>班级</th>
                <th v-for="subject in item.classInfo[0].subjectInfo">{{subject.subjectName}}</th>
              </tr>
              <tr v-for="classList in item.classInfo">
                <td v-for="classData in classList" v-if="classData == classList.className">{{classData}}</td>
                <td v-for="teacherData in classList.subjectInfo" :class="teacherData.teachingInfo.teacherName == '' ? 'AllowClick' : ''"  @click="getTarget(classList,teacherData,$event)">{{teacherData.teachingInfo.teacherName}}</td>
              </tr>
            </table>
          </div>
          <div>
              <ul>
                <li v-for="teacherData in selectSubject">{{teacherData.gradeName}}
                  <span>{{teacherData.className}}（{{teacherData.subjectName}}）</span>
                </li>
              </ul>
            <div>
              <el-button class="primary" type="primary" :disabled="state"  @click="submitSubject">保存</el-button>
              <el-button  type="text"  @click="skipSubject">跳过</el-button>

            </div>
          </div>
        </div>
        <div v-else-if="headTeacherClassShow">
          <h3>请选择一个班级登录操作</h3>
          <ul>
            <li class="schoolNameStyle" @click="selectClass(classInfo)" v-for="classInfo in headTeacherClassList">{{classInfo.gradeName}}（{{classInfo.className}}）</li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name:"hello",
    data: function(){
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          var patrn=/^(\w){6,19}$/;
          if (!patrn.exec(value)) {
//                  this.$refs.ruleForm2.validateField('checkPass');
            callback(new Error('至少6位不包含空格及特殊符号的密码'));
          }else{
            callback();
          }
        }
      };
      return {
        ruleForm: {
          password: '',
        },
        rules: {
          password: [
            { validator:validatePass, trigger: 'blur' }
          ]
        },
        state:false,
        userInfo : {
            name:'',
            id:''
        },
        // 选取教师
        teachingInfo:'',
        // 获取班级的科目名称
        subjectInfo:'',
        gradeList:[],
        currentGradeId:'',
        selectSubject:[],
        roleId:'', //权限身份id
        needResetPassword:false, //判断是否需要重置密码
        schoolLength : '',  //当前角色学校数量
        schoolList:[],   //角色对应多个学校列表
        schoolListIsShow:false, // 学校列表是否展示
        chooseSchoolId : '',//当前选择学校id
        needChooseObject:false, // 是否需要选择科目
//        chooseGrade:'',
        headTeacherClassList:[], // 班主任所带班级列表
        headTeacherClassShow:false,
      }
    },
    mounted(){
            const self = this;
      self.$ajax('next',
      ).then(function (response) {
        console.log(response);
        if (response.code == 200){
            self.userInfo.name = response.data.name;
            self.userInfo.id = response.data.teacherId;
            self.needResetPassword = response.data.needResetPassword;
            self.schoolLength = response.data.schools.length;
            self.schoolList = response.data.schools;
            var classLength;
            //TODO
            if (self.schoolLength > 1){
              self.schoolListIsShow = true;
            }else if (self.schoolLength <= 0){
//                学校列表为空,
              self.schoolListIsShow = false;
              self.needChooseObject = false;
              self.roleId = response.data.topRole.roleId;
//              判断是否为机构管理员身份
              if (self.roleId == 32){
                var userInfo = self.userInfo;
                userInfo.roleId = self.roleId;
                //              存储admin机构管理员信息
                userInfo = JSON.stringify(userInfo);
                localStorage.setItem('userInfo',userInfo);
                self.$router.push('/home');
              }else if(self.roleId== 4 ){
                  // 教育局登录
                var userInfo = self.userInfo;
                userInfo.roleId = self.roleId;
                userInfo.education = response.data.education;
                //              存储教育局登录信息
                userInfo = JSON.stringify(userInfo);
                localStorage.setItem('userInfo',userInfo);
                self.$router.push('/home');
              }
            }else {
              self.schoolListIsShow = false;
              self.needChooseObject = response.data.schools[0].needChooseObject;
              self.roleId = response.data.topRole.roleId;
              if (self.schoolLength == 1 && self.needChooseObject == false){
                  if (self.roleId == 1){
                    self.headTeacherClassShow = true;
                  }else {
                    var userInfo = self.userInfo;
                    userInfo.roleId = self.roleId;
                    userInfo.schools = response.data.schools[0];
                    //              存储普通教师登录信息
                    userInfo = JSON.stringify(userInfo);
                    localStorage.setItem('userInfo',userInfo);
                    self.$router.push('/home');
                  }

              }else if(self.schoolLength == 1 && self.needChooseObject == true){
                //判断是否需要选择教学信息
                if(self.needChooseObject == true){
                  self.$ajax('teacher/findTeachingBySchoolId',
//            {"schoolId":school.schoolId}
                    {"schoolId":1}
                  ).then(function (response) {
//            console.log(response);
                    if (response.code == 200){
                      self.gradeList = response.data;
                      self.currentGradeId = response.data[0];
                    }else {
                      self.$alert(response.message, '提示', {
                        confirmButtonText: '确定',
                        callback: action => {}
                      });
                    }
                  }).catch(function (error) {
                    console.log(error);
                  });
                }else if (self.roleId == 1){
                  //选择完科目的班主任
                  self.$ajax('headteacher/classes',
                    {"schoolId":self.chooseSchoolId,"teacherId":self.userInfo.id}
                  ).then(function (response) {
                    if (response.code == 200){
                      self.headTeacherClassList = response.data;
                      if(response.data.length > 1){
                        $this.needChooseObject = false;
                        $this.headTeacherClassShow = true;
                      }else {
                        //一个班级的情况直接跳转
                        var userInfo = self.userInfo;
                        userInfo.roleId = self.roleId;
                        userInfo.schoolId = self.chooseSchoolId;
                        userInfo.classInfo = response.data[0];
                        //              存储班主任信息
                        userInfo = JSON.stringify(userInfo);
                        localStorage.setItem('userInfo',userInfo);
                        self.$router.push('/home');
                      }
                    }else {
                      self.$alert(response.message, '提示', {
                        confirmButtonText: '确定',
                        callback: action => {}
                      });
                    }
                    console.log(response);
                  }).catch(function (error) {
                    console.log(error);
                  });
                }else if (self.roleId == 8 && self.roleId == 16){
                  // 选择完科目的普通教师和校长
                  var userInfo = self.userInfo;
                  userInfo.roleId = self.roleId;
                  userInfo.schoolId = self.chooseSchoolId;
//              存储教师信息
                  userInfo = JSON.stringify(userInfo);
                  localStorage.setItem('userInfo',userInfo);
                  self.$router.push('/home');

                }else if (self.roleId == 2){
                  // 学校管理员
                  var userInfo = self.userInfo;
                  userInfo.roleId = self.roleId;
                  userInfo.schoolId = self.chooseSchoolId;
                  //              存储学校管理员信息
                  userInfo = JSON.stringify(userInfo);
                  localStorage.setItem('userInfo',userInfo);
                  self.$router.push('/home');
                }
              }

            } ;
        }else {
          self.$alert(response.message, '提示', {
            confirmButtonText: '确定',
            callback: action => {}
          });
        }
      }).catch(function (error) {
        console.log(error);
      });
    },
    methods: {
      submitForm(formName) {
        const self = this;
        self.$refs[formName].validate((valid) => {
          if (valid) {
            self.$ajax('password/change',
              self.ruleForm
            ).then(function (response) {
                if (response.code == 200){
                  self.$alert(response.message, '提示', {
                    confirmButtonText: '确定',
                    callback: action => {}
                  });
              }else {
                  self.$alert(response.message, '提示', {
                    confirmButtonText: '确定',
                    callback: action => {}
                  });
                }
            }).catch(function (error) {
              console.log(error);
            });

          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      skip(){
        this.needResetPassword = false;
      },
      //选择学校操作
      selectSchool(school){
          console.log(school);
        const self = this;
        this.schoolListIsShow = false;
        this.needChooseObject = school.needChooseObject;
        this.chooseSchoolId = school.schoolId;
        self.roleId = school.schoolTopRole.roleId;
        //判断是否需要选择教学信息
        if(this.needChooseObject == true){
          self.$ajax('teacher/findTeachingBySchoolId',
//            {"schoolId":school.schoolId}
            {"schoolId":1}
          ).then(function (response) {
//            console.log(response);
            if (response.code == 200){
              self.gradeList = response.data;
              self.currentGradeId = response.data[0];
            }else {
              self.$alert(response.message, '提示', {
                confirmButtonText: '确定',
                callback: action => {}
              });
            }
          }).catch(function (error) {
            console.log(error);
          });
        }else if (self.roleId == 1){
            //选择完科目的班主任
          self.$ajax('headteacher/classes',
            {"schoolId":self.chooseSchoolId,"teacherId":self.userInfo.id}
          ).then(function (response) {
            if (response.code == 200){
              self.headTeacherClassList = response.data;
              if(response.data.length > 1){
                $this.needChooseObject = false;
                $this.headTeacherClassShow = true;
              }else {
                //一个班级的情况直接跳转
                var userInfo = self.userInfo;
                userInfo.roleId = self.roleId;
                userInfo.schoolId = self.chooseSchoolId;
                userInfo.classInfo = response.data[0];
                //              存储班主任信息
                userInfo = JSON.stringify(userInfo);
                localStorage.setItem('userInfo',userInfo);
                self.$router.push('/home');
              }
            }else {
              self.$alert(response.message, '提示', {
                confirmButtonText: '确定',
                callback: action => {}
              });
            }
            console.log(response);
          }).catch(function (error) {
            console.log(error);
          });
        }else if (self.roleId == 8 && self.roleId == 16){
            // 选择完科目的普通教师和校长
          var userInfo = self.userInfo;
          userInfo.roleId = self.roleId;
          userInfo.schoolId = self.chooseSchoolId;
//              存储教师信息
          userInfo = JSON.stringify(userInfo);
          localStorage.setItem('userInfo',userInfo);
          self.$router.push('/home');

        }else if (self.roleId == 2){
          // 学校管理员
          var userInfo = self.userInfo;
          userInfo.roleId = self.roleId;
          userInfo.schoolId = self.chooseSchoolId;
          //              存储学校管理员信息
          userInfo = JSON.stringify(userInfo);
          localStorage.setItem('userInfo',userInfo);
          self.$router.push('/home');
        }
      },
      getCurrentGradeId(GradeId,){
//         console.log(GradeId);
//        this.currentGradeId = GradeId;
//        this.getTeachingMsg()
      },
//      添加选择科目状态
      getTarget(item,list,event){
//          console.log(item,list);
        let data = list;
        if (list.teachingInfo.teacherName == ""){
              data.classId = item.classId;
              data.className = item.className;
              data.gradeId = this.currentGradeId.gradeId;
              data.gradeName = this.currentGradeId.gradeName;
//              console.log(event.target.tagName)
              var queryRight = '<i class="el-icon-circle-check queryRight"></i>';
              if (event.target.innerHTML == "" && event.target.tagName == "TD"){
                event.target.innerHTML = queryRight;
                this.selectSubject.push(data);
//                console.log(this.selectSubject);


              }else if (event.target.tagName == "I"){
                event.target.parentNode.innerHTML = '';
                this.selectSubject.forEach( ( item, i ) => {
                  if(item == data){
                    this.selectSubject.splice(i, 1);
                  }
                });
              }else {
                event.target.innerHTML = '';
                this.selectSubject.forEach( ( item, i ) => {
                    if(item == data){
                      this.selectSubject.splice(i, 1);
                    }
                });
              }
//            console.log(item,list);
//            console.log(this.selectSubject);
          }
      },
//      提交勾选的学科
      submitSubject(){
          let subjectData = [];
          let sub = {};
          let self = this;
          if(this.selectSubject != "" && this.selectSubject.length>0){
            this.selectSubject.forEach( ( item, i ) => {
              let sub = new Object();
              sub.classId = item.classId;
              sub.subjectId = item.subjectId;
              sub.teacherId = this.userInfo.id;
              subjectData.push(sub);
            });
            subjectData = JSON.stringify(subjectData);
            this.$ajax('teacher/bitchSaveClassTeacherSubject',
              {"data":subjectData}
            ).then(function (response) {
              if (response.code == 200){
                self.$alert(response.message, '提示', {
                  confirmButtonText: '确定',
                  callback: action => {
                    self.judgeTeacher();
                  }
                });
              }else {
                self.$alert(response.message, '提示', {
                  confirmButtonText: '确定',
                  callback: action => {}
                });
              }
            }).catch(function (error) {
              console.log(error);
            });

          }else {
            this.$alert('请选择任课学科后再点击保存', '提示', {
              confirmButtonText: '确定',
              callback: action => {
//                  this.$message({
//                    type: 'info',
//                    message: `action: ${ action }`
//                  });
              }
            });
          }
//        console.log(JSON.stringify(this.selectSubject))
      },
//      判断当前教师角色，存储信息
      judgeTeacher(){
        if(this.roleId == 1){
//              角色为班主任
//              alert("班主任");
          let $this = this;
          this.$ajax('headteacher/classes',
            {"schoolId":$this.chooseSchoolId,"teacherId":$this.userInfo.id}
          ).then(function (response) {
            if (response.code == 200){
              $this.headTeacherClassList = response.data;
              if(response.data.length > 1){
                $this.needChooseObject = false;
                $this.headTeacherClassShow = true;
              }else {
                //一个班级的情况直接跳转
                var userInfo = $this.userInfo;
                userInfo.roleId = $this.roleId;
                userInfo.schoolId = $this.chooseSchoolId;
                userInfo.classInfo = response.data[0];
                //              存储班主任信息
                userInfo = JSON.stringify(userInfo);
                localStorage.setItem('userInfo',userInfo);
                $this.$router.push('/home');
              }

            }else {
              $this.$alert(response.message, '提示', {
                confirmButtonText: '确定',
                callback: action => {}
              });
            }
            console.log(response);
          }).catch(function (error) {
            console.log(error);
          });

        }else {
          //非班主任普通教师||校长
          var userInfo = this.userInfo;
          userInfo.roleId = this.roleId;
          userInfo.schoolId = this.chooseSchoolId;
//              存储教师信息
          userInfo = JSON.stringify(userInfo);
          localStorage.setItem('userInfo',userInfo);
          this.$router.push('/home');
        };
      },
//      取消提交学科
      skipSubject(){
        this.judgeTeacher();
      },
      selectClass(classInfo){
//          console.log(classInfo);
        var userInfo = this.userInfo;
        userInfo.roleId = this.roleId;
        userInfo.schoolId = this.chooseSchoolId;
        userInfo.classInfo = classInfo;
        //              存储班主任信息
        userInfo = JSON.stringify(userInfo);
        localStorage.setItem('userInfo',userInfo);
        this.$router.push('/home');
      }
    }
  }
</script>

<style>
  .hello .login-wrap{
    margin: 0 auto;
  }
  .hello .nav_container {
    width: 100%;
    height: auto;
    position: relative;
    z-index: 100;
    background: #fff;
  }
  .hello .nav-centre {
    width: 1024px;
    height: 80px;
    background: #fff;
    margin: 0 auto;
    overflow: hidden;
  }
  .hello .nav_container img {
    width: 150px;
    height: 38px;
    margin-top: 21px;
    float: left;
    margin-right: 57px;
  }
  .hello .contentBody {
    padding-top: 80px;
    width: 1024px;
    min-height: 774px;
    background: #ffffff;
    border-top: 5px solid #0C9EE9;
    margin:  0 auto;
    text-align: center;
  }
  .hello .ms-login{
    display: inline-block;
    width:280px;
    height:230px;
    padding:40px 30px 40px 14px;
    border-radius: 5px;
    background: #fff;
  }
  .hello .login-btn{
    margin-top: 40px;
    text-align: right;
  }
  .hello .loginBtn button{
    width: 100%;
  }
  .hello .login-btn button{
    width:30%;
    height:36px;
    margin-right: 11px;
  }
  .hello .pageTwo table {
    width: 100%;
    text-align: center;
  }
  .hello .pageTwo table th {
    text-align: center;
  }
  .hello .gradeWrap {
    text-align: left;
  }
  .hello .gradeWrap ul {
    display: inline-block;
    margin: 20px 0;
  }
  .hello .gradeWrap li {
    margin: 0 5px;
    display: inline-block;
    width: 100px;
    height: 35px;
    line-height: 35px;
    border: 1px solid grey;
    border-radius: 5px;
    text-align: center;
    cursor: pointer;
  }
  .hello .gradeWrap li:hover {
    background: lightblue;
    color: white;
    border: 1px solid rgba(0,0,0,0);
  }
  .hello .gradeWrap li.active {
    background: cornflowerblue;
    color: white;
    border: 1px solid rgba(0,0,0,0);
  }
  .hello .subjectMgt-body{
    margin-top: 10px;
  }
  .hello .subjectMgt-body table{
    width: 100%;
    text-align: center;
  }
  .hello .subjectMgt-body table th{
    text-align: center;
  }
  .hello .subjectMgt-body table th,
  .hello .subjectMgt-body table td{
    padding: 0;
    box-sizing: border-box;
    border: 1px solid #dfe6ec;
    height: 40px;
  }
  .hello .teacherName {
    display: inline-block;
    width: 100%;
    height: 35px;
    line-height: 35px;
  }
  .hello .teacherName >i {
    color: brown;
  }
  .hello .schoolSelect >ul {
    margin-top: 30px;
    text-align: center;
  }
  .hello .schoolNameStyle {
    display: block;
    height: 35px;
    color: grey;
    line-height: 35px;
    border-radius: 10px;
    width: 30%;
    margin: 20px 35%;
    cursor: pointer;
    border: 1px solid gray;
  }
  .hello .schoolNameStyle:hover{
    color: #007dc4;
    border: 1px solid #0C9EE9;
  }
  .hello .queryRight{
    color: brown;
  }
  .hello .AllowClick {
    cursor: pointer;
  }

</style>



